import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MyDashComponent } from './my-dash/my-dash.component';
import { OverallMachineComponent } from './overall-machine/overall-machine.component';
import { DetailMachineComponent } from './detail-machine/detail-machine.component';
import { DetailFailureModeComponent } from './detail-failure-mode/detail-failure-mode.component';
import { LoginComponent } from './login/login.component';

const routes: Routes = [
  { path: 'test', component: MyDashComponent },
  { path: 'login', component: LoginComponent },
  { path: 'overall-machine', component: OverallMachineComponent },
  { path: 'detail-machine', component: DetailMachineComponent },
  { path: 'detail-failure-mode', component: DetailFailureModeComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
